.pomo-settings-box {
	position: absolute;
	height: 100%;
	width: 100%;
	top: 0;
	left: 0;
	background-color: #111;
	z-index: 4;
	overflow: scroll;
}

.top-box {
	height: 12%;
}

.pomo-settings-timer, .pomo-settings-stats {
	background: #222;
	margin: 1rem;
	border-radius: 20px;	
}

.pomo-settings-header {
	padding: 1rem;
	margin-bottom: 0.5rem;
	font-size: 20px;
}
.pomo-settings-sub-header{
	display: flex;
	font-size: 12px;
	justify-content: space-between;
	font-weight: bold;
}

.pomo-settings-sub-header h3{
	font-weight: normal;
}

.pomo-settings-sub-header h3:last-child{
	opacity: 0.5;
}

.exit-more-icon {
	position: absolute;
	top: 18px;
	left: 18px;
	height: 18px;
	width: 18px;
	z-index: 4;
}

.break-settings, .work-settings {
	padding: 0 1rem;
}


 
.adjust-break, .adjust-work {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	top: 30%;
	left: 50%;
	transform: translate(-50%, -50%);
	font-size: 24px;
	width: 100%;
}

.timer-settings {
	overflow: hidden;
		position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	top: 30%;
	left: 50%;
	transform: translate(-50%, -50%);
	font-size: 24px;
	width: 100%;
}

.timer-settings ul {
	text-align: center;
	padding-right: 1em;
	font-size: 32px
}

.adjust-break p, .adjust-work p {
	text-align: center;
	padding-right: 10px;
	padding-left: 10px;
	font-size: 32px
}


.buttons {
	height: 20px;
	width: 100px;
	background-color: inherit;
	color: #fff;
	border-style: none;
}

.buttons:hover {
	opacity: 0.5;
}

.timer-inactive {
	opacity: 0.5;
}

.active-box {
	height: 45px;
	width: 30px;
	border: solid thin #efefef99;
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
}