*{
	transition: opacity 0.5s;
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}

body {
	font-family: Roboto,RobotoDraft,Helvetica,Arial,sans-serif;
	overflow: hidden;
	color: black;

}

*::-webkit-scrollbar {
  display: none;
}

div {
	display: block;
}

button {
	background: none;
	border: none;
	color: #fff;
}

button:focus {
	outline:0;
}
/*Front page layout*/

.App {
	display: grid;
	grid-template: repeat(6, 1fr) / repeat(10, 1fr);
	color: #fff;
	transition: background 0.3s;
}

.App::before {
  content: "";
  position: absolute;
  top: 0; left: 0;
  width: 100%; height: 100%;
  background: #3C7290 no-repeat;
  background: url('./background-4.jpg') no-repeat;
  background-size: cover;
  background-attachment: fixed;	
  z-index: -2;
}


.App > div:nth-child(1), .App > div:nth-child(4) {
	border-left: none;
}

.App > div:nth-child(5), .App > div:nth-child(6) {
	border-bottom: none;
}
 .App > div:nth-child(4),  .App > div:nth-child(5),  .App > div:nth-child(6) {
	border-right: none;
}

/*.App > div:last-child {
	border: none;
}*/

.box-one {
	max-width: 100px;
	grid-row: 1/2;
	grid-column: 1/2;
}

.box-two {
	grid-row: 2/7;
	grid-column: 1/6;
		overflow: scroll;
}
 
.box-three {
	grid-column: 6 / 8;
	grid-row: 1 / 4;
}

.box-three:hover .pomo-container {
	display: block;
	opacity: 1;
}

.box-four {
	grid-column: 8/10;
	grid-row: 1/4;
	
}
.box-four:hover .weather-container {
	display: block;
	opacity: 1;
}

.box-five {
	grid-column: 6/10;
	grid-row: 4/7;
}
.box-five:hover .news-container  {
	display: block;
	opacity: 1;
}

.box-six {
	position: relative;
	grid-column: 10/11;
	grid-row: 1/7;
	z-index: 5;
}

.box-six:hover .red {
	transform: translateX(0%);
}

.settings-label {
	z-index: 5;
	opacity: 1;
}



.red {
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	background: #8F4A4F;
	/*transform: translateX(100%);*/
	transition: all 0.4s ease-in-out;
	z-index: -1
}

.blue {
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	background: #3C728F;
	/*transform: translateX(100%);*/
	transition: all 0.4s ease-in-out;
	z-index: 0
}

#pomo-pin, #weather-pin, #news-pin {
	opacity: 0;
}


.open-settings {
	width: 50vw;
	transform: translateX(-470px) !important;
	z-index: 4;
}

.trans-x {
	transform: translateX(100%);
}




/*Component Contianers*/

.item {
	border: solid 1px #fff;
	position: relative;
	border-top: none;


}

.grow {
	transition: all 0.2s ease-in;
}

.grow:hover {
	transform: scale(1.3) translateY(-5px);
}

.show {
	opacity: 1 !important;
	display: block !important;

}

.stick {
	transform: translateY(0%);
}

.widget-labels {
	font-size: 16px;
	color: inherit;
	padding: 10px;
	position: absolute;
	bottom: 0;
	font-weight: 100;
}

.widget-labels:hover {
	cursor: pointer
}


