@media only screen and (max-width: 1024px) {

	.box-one {
		display: none;
		}

	.box-two {
	grid-row: 1/3;
	grid-column: 1/9;
}
 
.box-three {
	grid-column: 1 / 5;
	grid-row: 3 / 5;
}


.box-four {
	grid-column: 5/9;
	grid-row: 3/5;
}

.box-five {
	grid-column: 1/9;
	grid-row: 5/7;
}

.box-six {
	position: relative;
	grid-column: 9/11;
	grid-row: 1/7;
}


.open-settings {
	width: 90vw;
	z-index: 4;
}


.App > div:nth-child(3), .App > div:nth-child(4) {
	border-top: solid 1px;
}
.App > div:nth-child(3), .App > div:nth-child(5) {
	border-left: none;
}

}

li {
	list-style: none;
}