.pomo-container {
	display: block;
	width: 100%;
	height: 100%;
	opacity: 0;
	z-index: 4;
}

.pomo-top-section {
	position: relative;
	height: 10%;
	z-index: 4;
}

.pomo-top-section img {
	height: 24px;
	width: 24px;
	transition: all 0.5s;
}

.more-icon {
	position: relative;
	float: left;
	top: 15px;
	left: 15px;
}

.stats-icon {
	position: relative;
	float: right;
	top: 15px;
	right: 15px;
	height: 24px;
	width: 24px;
	fill: #fff;
}

.pomo-middle-section {
	position: relative;
	height: 70%;
	z-index: 4;
}

.countdown-ring {
	position: absolute;
	width: 175px;
	height: 175px;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	border-radius: 100%;
	border-style: solid;
	border-width: 2px;
	border-color: #fff;
	background-color: rgba(1, 1, 1, 0);
}

.countdown-expanding-ring {
	display: none;
	position: absolute;
	width: 175px;
	height: 175px;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	border-radius: 100%;
	border-style: solid;
	border-color: #fff;
	background: #00000000;
	animation: rign-motion 4s infinite ease-out;
}

@keyframes rign-motion {
	from {
		width: 175px;
		height: 175px;
		opacity: 0.5;
	}

	to {
		width: 220px;
		height: 220px;
		opacity: 0;
	}
}

.pomo-pause-container {
	position: absolute;
	width: 100%;
	height: 100%;
	background: #100F0Faa;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	display: none;
	z-index: 5;
}

.pomo-pause-content-wrapper {
	position: relative;
	width: 200px;
	height: 70%;
	background: #100F0Fee;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	border-radius: 20px;
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
	text-align: center;
	padding: 0 1em;
}

.pause-item {
	padding: 0.3em 0;
}

.pause-icon {
	width: 50px;
	height: 50px;
	fill: skyblue;
}

.pomo-exit-button {
	position: absolute;
	width: 30px;
	height: 30px;
	fill: #efefef;
	transform: translate(-50%, -50%);
	top: 50%;
	left: 50%;
	display: none;
	opacity: 0;
	transition: all 0.5s;
	cursor: pointer;
}

.countdown-ring:hover .pomo-exit-button {
	opacity: 1;
	transform: translate(-50%, -150%);
}

.pause-header {
	font-weight: normal;
	font-size: 16px;

}

.pause-sub-header {
	font-weight: normal;
	font-size: 10px;
	opacity: 0.5;
}

.pause-button {
	margin-top: 1.5em;
	padding: 0.5em 1.5em;
	border-radius: 15px;
	background: #D88D21;
}

.countdown {
	position: absolute;
	font-family: 'arial';
	font-weight: 100;
	font-size: 36px;
	margin: 0;
	transform: translate(-50%, -50%);
	top: 50%;
	left: 50%;
}

.pomo-bottom-section {
	height: 20%;
	position: relative;
	z-index: 4;
}


.pomo-button-box {
	position: absolute;
	left: 50%;
	top: 10%;
	transform: translateX(-50%);
}

.pomo-button {
	background-color: rgba(1, 1, 1, 0);
	font-family: 'arial';
	font-weight: lighter;
	font-size: 16px;
	color: #fff;
	border-radius: 15px;
	border-color: #fff;
	border-style: solid;
	border-width: 2px;
	cursor: pointer;
	padding: 0.5em 1.5em;
	transition: 0.5s;
}

.pomo-button:hover {
	opacity: 0.5;
}

.pomo-exit-button {
	display: none;
}

@media only screen and (max-width: 1024px) {
.countdown-ring {
	width: 150px;
	height: 150px;

}
}