.swiper-slide {
	text-align: center;
	overflow: auto;
}

.swiper-container {
	position: relative;
	opacity: 100%;
	padding: 1em 0;
}

.swiper-container::before {
	content: '';
	width: 40%;
	height: 30px;
	position: absolute;
	left: 0;
	top: 0;
	background: #222;
	opacity: 80%;
	z-index: 100;
}

.swiper-container::after {
	content: '';
	width: 40%;
	height: 30px;
	position: absolute;
	top: 0;
	right: 0;
	background: #222;
	opacity: 80%;
	z-index: 100;
}



.swiper-scrollbar {
	top: 30px;
	position: relative;
}


