.settings-right-side {
	width: 70%;
}

.general-settings {
  display: block
}
.backgrounds-settings {
  display: block
}
.quotes-settings {
  display: block
}



.right-side-body {
  position: relative;
	margin-left: 10%;
	margin-right: 2%;
  margin-top: 5%;
  margin-bottom: 5%;
	color: rgba(255, 255, 255, 0.95);
	font-family: Helvetica;

  overflow-y: scroll;
}

.right-side-body::-webkit-scrollbar-track
{
  height: 50%;
} 

.right-side-body::-webkit-scrollbar
{
  width: 12px;
}

.right-side-body::-webkit-scrollbar-thumb
{
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
  background-color: rgba(255, 255, 255, 0.1);
}

.options-heading {
  border-bottom-style: solid;
  border-bottom-color: rgba(255, 255, 255, 0.1);

}

.options-heading h1 {
	margin-bottom: 0;
}

.options-heading p {
	margin-top: 0; 
	color: rgba(255, 255, 255, 0.6);
}

.selected-options-item {
	padding-bottom: 15px;
  padding-right: 15px;
}

.settings-sub-heading {
  padding: 1em 0;
  font-size: 16px;
  color: #fff;
}


/*switch*/

.toggle-switch {
	position: relative;
  	display: inline-block;
	float: right;
	height: 25px;
	width: 40px;
	cursor: pointer;
	transition: 0.4s;
}

.toggle-switch input{
	opacity: 0;
	width: 0;
	height: 0;

}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 34px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  bottom: 2px;
  left: 2px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 50%;
}


input.checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input.checked + .slider:before {
  -webkit-transform: translateX(16px);
  -ms-transform: translateX(16px);
  transform: translateX(16px);
}

.toggle-options {
  position: relative;
  display: inline-block;
  float: right;
  font-size: 0.8rem;
  opacity: 0.8;
  padding-top: 4px;
}

.hr24, .hr12, .blue-red, .navy-wine {
  cursor: pointer;
  opacity: 0.5;
}

.active {
  opacity: 1;
}

.hide {
  display: none; 
}

.fade-in {
  opacity: 1;
}
.fade-out {
  opacity: 0;
}
