.weather-list-container {
	color: #fff;
	width: 100%;
	height: 100%;
		
}

.weather-button-container {
	margin: auto;
	width: 100%;
	height: 100%;
	display: none;
	opacity: 1;

}

.weather-container {
	padding: 1.5em;
	opacity: 0;
	position: relative;
	z-index:4;
	width: 100%;
	height: 100%;
	overflow: scroll;
}

.weather-location {
	font-size: 28px;
}

.weather-container::before {
	content:"";
	border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid #000000CC;	
    position: absolute;
	top: -10px;
	right: 25px;
}

.current-weather-container {
	display: flex;
	justify-content: space-between;
	margin: 2em 0 1em 0;
}

.current-weather-description {
	font-size: 20px
}

.current-weather-content {
	margin-top: 4px;
}

.current-weather-temp {
	font-size: 24px;
}

.current-weather-icon img {

	height: 70px;
	width: 70px;
}

.five-day-forcast-container {
	display: flex;
	justify-content: space-between;
	overflow: scroll;
}
