.news-card-container {
	position: relative;
	display: flex;
	justify-content: center;
	padding: 2em;
border-bottom: solid 1px #ffffff20;
}

.news-card-image-container {
	flex: 1;
	display: flex;
	justify-content: center;
}

.news-card-image {
	height: 100px;
	width: 100px;
	object-fit: cover;
	border-radius: 5%;

}

.news-card-content {
	flex: 3;
	margin: auto;
		
}

.news-card-publication {
	font-size: 12px;
	opacity: 0.8;
	display: flex;
	padding: 0.5em 0;
}

.news-card-author {
	padding-right: 0.3em;
}

.news-card-title {
	font-size: 20px;
	font-weight: 400;
}
.news-card-container a:hover {
	text-decoration: underline;
}


