.settings-left-side {
	width: 30%;
	border-right-style: solid;
	border-right-color: rgba(255, 255, 255, 0.1);
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 100%;

}

.options-wrapper {
	padding-top: 25%;
	font-family: Helvetica;
	
	font-weight: 100;
	letter-spacing: 1px;
	color: rgba(255, 255, 255, 0.8);
	display: flex;
	flex-direction: column;
}

.options {
	padding: 5% 20%;
	font-size: 16px; 
}

.options:hover {
	opacity: 0.5;
	cursor: pointer;
}


.user-box {
	font-family: Helvetica;
	font-size: 12px; 
	font-weight: 100;
	letter-spacing: 1px;
	transition: all 0.3s;
	padding-bottom: 10%;
}

.user-box__image {
	width: 36px;
	height: 36px;
}

.user-box__button {
	background-color: inherit;
	border: none;
	color: rgba(255, 255, 255, 0.8);
	padding: 0;
	height: 16px;
	width: 16px;
}

.user-box__options {
	padding: 5% 20%;
}

.user-box__button img {
	height: 100%;
	width: 100%;
}

.user-box__line {
	display: flex;
	align-items: center;
	justify-content: space-around;
	padding: 10% 15%;
}


.user-box:hover {
	background-color: rgba(150, 150, 150, 0.1);
}

.user-box__auth {
	display: flex;
	flex-direction: column;
}

.trans-y {
	transform: translateY(75px);
}

.trans-rot {
	transform: rotate(180deg);
}

@media only screen and (max-width: 600px) {
	.settings-left-side {
		display: none
	}
 }
