
.settings-container {
	position: absolute;
	display: none;
	width: 600px;
	height: 100%;
	background-color: inherit;
	border-radius: 5%;
	opacity: 0;
	transition: opacity 0.5s;
	z-index: 4;
	color: #fff;
}
.settings-right-side {
	overflow: scroll;
}

.settings-flex {
	display: flex;
	height: 100%;
}
