.weather-card {
	text-align: center;
	color: #eeefff;
	padding: 4px 10px;
	display: flex;
	flex-direction: column;
	align-items: center;
}



.weather-card img {
	margin-top: 4px;
	height: 40px;
	width: 40px;
}

.weather-card p {
	font-size: 16px;
}

.weather-card h4 {
	font-size: 16px;
}
